.about-main-container{
  background-color: #f7f7f7;
}

.about-banner {
    margin: 0;
    padding: 0;
    background: url('/src/components/imgs/Ariel_Truck.jpg') no-repeat center/cover;
    height: 40vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
  
  }

.about {
    background-color: #f7f7f7;
  }
  
  .about-container {
    max-width: 100%;
    text-align: center;
  }
  
  .about h2 {
    font-size: 3rem;
    margin-bottom: 3rem;
    font-family: 'Fjalla One', sans-serif;
  }

  .about__main__container{
    padding: 3rem 0;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    place-items: center;
  }

  .about__main__container__item{
    display: flex;
    align-items: center;
    padding: 5% 0;
  }

  .about__main__container__item__img {
    width: 450px;
    padding: 2rem;
  }

  .about__item__1{
    /* background-color: #001F3F; */
    background-color: #023047;
    color: white;
  }

  .about__item__2{
    flex-direction: row-reverse;
    /* background-color: #ECC30B; */
    background-color: #219ebc;
    /* color: #001F3F; */
    color: white;
  }
  .about__item__3{
    /* background-color: #e8e8e8; */
    background-color: #8ecae6;
    color: #001F3F;
  }

  .about__main__container p{
    text-align: center;
    padding:0 15%;
    font-size: 24px;
  }
  .about__main__container h3{
    text-align: center;
    font-size: 2rem;
  }
  
  .grid-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    justify-items: center;
    text-align: center;
    font-size: 1.5rem;
    margin: 2rem;
}

.grid-item {
    width: 100%;
    padding: 1rem;
    border-radius: 4px;
    border: none;
    background-color: #d9d9d9;
    font-family: 'Cormorant Garamond', serif;
    font-size: 1.5rem;
  }

  .about-span{
    text-transform: uppercase;
  }

  .about-btn-container{
    display: flex;
    justify-content: center;
    padding: 3rem;
  }

  .btn-call{
    background-color: #102E4A;
    color: #ECC30B;
    padding: 1rem;
    border: none;
    border-radius: 4px;
    text-decoration: none;
    text-transform: uppercase;
    transition: .3s;
  }

  .btn-call:hover{
    background-color: #ECC30B;
    color: #102E4A;
    font-weight: bold;
  }
  
  /* Media Queries */
  @media (max-width: 1023px){
    .about__main__container{
      padding: 0;
      display: grid;
      grid-template-rows: repeat(1, 1fr);
      place-items: center;
    }

    .about__main__container p{
      text-align: center;
      padding:2rem;
      font-size: 24px;
    }
  
    .about__main__container__item{
      display: flex;
      align-items: center;
      flex-direction: column;
      padding-top: 3rem;
    }
  }

  @media (max-width: 987px) {
    .grid-container {
      grid-template-columns: 1fr;
      gap: 1rem;
    }

  }

  @media (max-width: 453px) {
    .grid-container {
      margin: .5rem;
    }

    .about__main__container{      
      padding: 0;
    }

    .about__main__container p{
      text-align: center;
      padding:1rem;
      margin-bottom: 1rem;
      font-size: 24px;
      
    }

    .about__main__container__item__img {
      width: 315px;
      margin-left: 0;
    }
  }
  