
@import url('https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap');


.landing {
    background: url('/src/components/imgs/towing_red_car_f.jpg') no-repeat center/cover;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
    text-align: center;
  }
  
  .landing h1 {
    font-size: 4rem;
    margin: 2rem;
    font-family: 'Fjalla One', sans-serif;
    font-weight: 100;
    text-transform: uppercase;
  }
  
  .landing p{
    font-family: 'Cormorant Garamond', serif;
    font-size: 2rem;
    margin: 2rem;
    font-weight: bolder;
  }

  .home-btn-call {
    background-color: #fff;
    color: #333;
    font-weight: bolder;
    text-transform: uppercase;
    margin: 2rem;
    padding: 1.5rem 2rem;
    text-decoration: none;
    border: none;
    cursor: pointer;
    transition: .5s;
  }
  .home-btn-call:hover{
    background-color: #102E4A;
    color: #ECC30B;
  }

  .us__container{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem 4rem;
    background: #FEFEFF;
    background: linear-gradient(135deg, #FEFEFF, #B1B1B1);
  }

  .us__container h1{
    font-size: 4rem;
    font-family: 'Fjalla One', sans-serif;
    font-weight: 100;
    text-transform: uppercase;
    color: #333;
  }
  .us__container h2{
    font-size: 3rem;
    font-family: 'Fjalla One', sans-serif;
    font-weight: 100;
    text-transform: uppercase;
    color: #333;
  }

  .us__container p{
    font-family: 'Cormorant Garamond', serif;
    font-size: 1.5rem;
    padding: 0rem 4rem;
  }



  /* review section */
  .review-section {
    padding: 1rem 4rem ;
    /* background-color: #c6c6c6; */
    background-color: #ebebeb;
  }
  
  .review-container {
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
  }
  
  .review-section h2 {
    font-size: 3rem;
    margin-bottom: 2rem;
    font-family: 'Fjalla One', sans-serif;
  }
  
  .reviews {
    display: flex;
    justify-content: center;
    align-items: stretch;
    gap: 2rem;

  }
  
  .review {
    flex: 1;
    padding: 1.5rem;
    border-radius: 5px;
    text-align: left;
    font-family: 'Cormorant Garamond', serif;
    font-size: 1.5rem;
    background-color: #dedede;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  
  .rating {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    color: #ECC30B;
  }
  
  .author {
    font-style: italic;
    margin-top: 1rem;
  }
  
  /* Media Queries */
  @media (max-width: 987px) {
    .landing h1{
      font-size: 2rem;
    }

    .landing p{
      font-size: 1.5rem;
    }

    .review-section {
      padding: 1rem ;
    }

    .reviews {
      flex-direction: column;
    }

    .review {
      flex: 1;
      padding: 1.5rem;
      border-radius: 5px;
      text-align: left;
      font-family: 'Cormorant Garamond', serif;
      font-size: 1.3rem;
      background-color: #dedede;
    }
  }

  
  @media (max-width: 567px) {
    .review-section {
      padding:  1rem ;
    }
    
    .us__container{
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 1rem;

  }
  
  .us__container h1{
    font-size: 2rem;
    font-family: 'Fjalla One', sans-serif;
    font-weight: 100;
    text-transform: uppercase;
    color: #333;
  }

  .us__container h2{
    font-size: 1.5rem;
    font-family: 'Fjalla One', sans-serif;
    font-weight: 100;
    text-transform: uppercase;
    color: #333;
  }
  
  .us__container p{
    font-family: 'Cormorant Garamond', serif;
    font-size: 1.2rem;
    padding: 0;
  }
}

@media (max-width: 374px) {
  .reviews {
    gap: 1rem;
  }

  .review p{
    font-size: 1.1rem;
  }
}










